@import "./fonts/SourceCodePro/SourceCodePro.css";
@import "./fonts/Handlee/Handlee.css";

:root {
    --primary-color-nerd: #323EDD;
    --secondary-color-nerd: #4D089A;
    --primary-color-normie: #ffd000;
    --secondary-color-normie: #8c00ff;
}

/* @media (prefers-color-scheme: dark) { */
    html {
        color-scheme: dark;
        background-color: #121212;
        color: #f1f1f1;
    }
    
    .content-profession, .content-header {
        font-family: "Source Code Pro";
        font-weight: 700;    
    }

    .content-personal {
        font-family: "Handlee";
        font-weight: 700;
        font-size: larger;
    }
/* } */
    
.wrong_code {
    text-decoration-line: underline;
    text-decoration-style: wavy;
    text-decoration-color: red;
    text-decoration-skip-ink: none;
    text-decoration-thickness: 2%;
}


/* The switch - the box around the slider */
.switch {
    --slider-transition-delay: .4s;
    --slider-base-size: 10px;

    position: relative;
    display: inline-block;
    width: calc(var(--slider-base-size) * 15);
    height: calc(var(--slider-base-size) * 6.5);
}

/* Hide default HTML checkbox */
.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

/* The slider */
.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-image: linear-gradient(45deg, var(--primary-color-nerd), var(--secondary-color-nerd));
    -webkit-transition: var(--slider-transition-delay);
    transition: var(--slider-transition-delay);
}

.slider:before {
    position: absolute;
    content: "";
    height: calc(var(--slider-base-size) * 4.5);
    width: calc(var(--slider-base-size) * 6.5);
    left: var(--slider-base-size);
    bottom: var(--slider-base-size);
    background-color: white;
    -webkit-transition: var(--slider-transition-delay);
    transition: var(--slider-transition-delay);

    content: "Nerd";
    font-family: "Source Code Pro";
    font-weight: 700;
    align-items: center;
    align-content: center;
    text-align: center;
    color: black;
    font-size: calc(var(--slider-base-size)*1.5);
}

input:checked+.slider {
    background-image: linear-gradient(45deg, var(--primary-color-normie), var(--secondary-color-normie));
}

input:focus+.slider {
    box-shadow: 0 0 1px var(--primary-color-nerd);
}

input:checked+.slider:before {
    -webkit-transform: translateX(calc(var(--slider-base-size) * 6.5));
    -ms-transform: translateX(calc(var(--slider-base-size) * 6.5));
    transform: translateX(calc(var(--slider-base-size) * 6.5));
    content: "Normie";
    font-family: "";
}

/* Rounded sliders */
.slider.round {
    border-radius: calc(var(--slider-base-size)*7.5);
}

.slider.round:before {
    border-radius: calc(var(--slider-base-size)*7.5);
}

/* The gradient font */
.gradient-text {
    background: linear-gradient(45deg, var(--primary-color-normie), var(--secondary-color-normie));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
    width: fit-content;
}